<template>
  <div
    :id="`${id}-DatePicker`"
    :class="{
      'flex-1 inline': inline,
      'p-0 range flex-1': range,
      'is-dark': dark,
      'has-shortcuts': range && !noShortcuts,
    }"
    class="datepicker-container flex flex-fixed"
  >
    <RangeShortcuts
      v-if="range && !noShortcuts"
      ref="range-shortcuts"
      :value="shortcut"
      :color="color"
      :dark="dark"
      :custom-shortcuts="customShortcuts"
      :height="height"
      @change-range="$emit('input', $event)"
    />
    <div class="calendar lm-w-100">
      <div class="datepicker-controls flex align-center justify-content-center">
        <div class="arrow-month h-100">
          <button
            type="button"
            tabindex="-1"
            class="
              datepicker-button datepicker-prev
              text-center
              h-100
              flex
              align-center
            "
            @click="changePeriod('prev')"
          >
            <svg viewBox="0 0 1000 1000">
              <path
                d="M 336.2 274.5 l -210.1 210 h -22.1 c 13 0 24 -45.5 23 23 s -10 23 -23 23 H 126.1 l 210.1 210.1 c 11 11 11 21 0 32 c -5 5 -10 7 -16 7 s -11 -2 -16 -7 l -249.1 -249 c -11 -11 -11 -21 0 -32 l 249.1 -249.1 c 21 -21.1 53 10.9 32 32 z"
              />
            </svg>
          </button>
        </div>
        <div
          class="datepicker-container-label flex-1 flex justify-content-center"
        >
          <TransitionGroup
            v-if="!monthsOnly"
            :name="transitionLabelName"
            class="h-100 flex align-center flex-1 flex justify-content-right"
          >
            <CustomButton
              v-for="m in [month]"
              :key="m.month"
              class="date-buttons lm-fs-16 padding-button flex-1"
              :color="color"
              :dark="dark"
              @click="selectingYearMonth = 'month'"
            >
              {{ monthFormatted }}
            </CustomButton>
          </TransitionGroup>
          <TransitionGroup
            :name="transitionLabelName"
            class="h-100 flex align-center flex-1 flex justify-content-left"
            :class="{'justify-content-center': monthsOnly }"
          >
            <CustomButton
              v-for="y in [year]"
              :key="y"
              class="date-buttons lm-fs-16 padding-button flex-1"
              :color="color"
              :dark="dark"
              @click="selectingYearMonth = 'year'"
            >
              {{ year }}
            </CustomButton>
          </TransitionGroup>
        </div>
        <div class="arrow-month h-100 text-right">
          <button
            type="button"
            tabindex="-1"
            class="
              datepicker-button datepicker-next
              text-center
              h-100
              flex
              align-center
              justify-content-right
            "
            @click="changePeriod('next')"
          >
            <svg viewBox="0 0 1000 1000">
              <path
                transform="scale(-1, 1) translate(-1000, 0)"
                d="M 336.2 274.5 l -210.1 210 h -22.1 c 13 0 24 -45.5 23 23 s -10 23 -23 23 H 126.1 l 210.1 210.1 c 11 11 11 21 0 32 c -5 5 -10 7 -16 7 s -11 -2 -16 -7 l -249.1 -249 c -11 -11 -11 -21 0 -32 l 249.1 -249.1 c 21 -21.1 53 10.9 32 32 z"
              />
            </svg>
          </button>
        </div>
      </div>
      <WeekDays
        v-if="!monthsOnly"
        :week-days="weekDays"
        :dark="dark"
      />
      <div
        :style="{
          height: monthDays.length + weekStart > 35 ? '250px' : '210px',
        }"
        class="month-container"
      >
        <TransitionGroup
          v-if="!monthsOnly"
          :name="transitionDaysName"
        >
          <div
            v-for="m in [month]"
            :key="m.month"
            class="datepicker-days flex"
          >
            <div
              v-for="start in weekStart"
              :key="start + 'startEmptyDay'"
              class="datepicker-day align-center justify-content-center"
            />
            <button
              v-for="day in monthDays"
              :key="day.format('D')"
              :class="{
                selected: isSelected(day) && !isDisabled(day),
                disabled: isDisabled(day) || isWeekEndDay(day),
                enable: !(isDisabled(day) || isWeekEndDay(day)),
                between: isBetween(day) && range,
                first: firstInRange(day) && range,
                last: lastInRange(day) && !!value.end && range,
              }"
              :disabled="isDisabled(day) || isWeekEndDay(day)"
              type="button"
              tabindex="-1"
              class="datepicker-day flex align-center justify-content-center"
              @click="selectDate(day)"
            >
              <span
                v-if="isToday(day)"
                class="datepicker-today"
              />
              <span
                v-show="!isDisabled(day) || isSelected(day)"
                :style="bgStyle"
                class="datepicker-day-effect"
              />
              <span
                v-if="isKeyboardSelected(day)"
                class="datepicker-day-keyboard-selected"
              />
              <span class="datepicker-day-text flex-1">
                {{ day.format("D") }}
              </span>
            </button>
            <div
              v-for="end in endEmptyDays"
              :key="end + 'endEmptyDay'"
              class="datepicker-day flex align-center justify-content-center"
            />
          </div>
        </TransitionGroup>

        <YearMonthSelector
          v-else
          :locale="locale"
          :color="color"
          :dark="dark"
          mode="month"
          :month="month"
          :hide-close="true"
          @input="selectYearMonth"
        />
      </div>

      <YearMonthSelector
        v-if="selectingYearMonth"
        :locale="locale"
        :color="color"
        :dark="dark"
        :mode="selectingYearMonth"
        :month="month"
        @input="selectYearMonth"
        @back="selectingYearMonth = null"
      />
    </div>
  </div>
</template>

<script>
  import moment from 'moment'
  import { getWeekDays } from '@/VueCtkDateTimePicker/modules/month'
  import RangeShortcuts from './_subs/RangeShortcuts'
  import YearMonthSelector from './_subs/YearMonthSelector'
  import WeekDays from './_subs/WeekDays'
  import CustomButton from '@/VueCtkDateTimePicker/_subs/CustomButton'

  export default {
    name: 'DatePicker',
    components: {
      RangeShortcuts,
      YearMonthSelector,
      WeekDays,
      CustomButton
    },
    props: {
      id: { type: String, default: null },
      value: { type: [String, Object], default: null },
      shortcut: { type: String, default: null },
      color: { type: String, default: null },
      minDate: { type: String, default: null },
      maxDate: { type: String, default: null },
      locale: { type: String, default: null },
      inline: { type: Boolean, default: null },
      noWeekendsDays: { type: Boolean, default: null },
      disabledWeekly: { type: Array, default: () => [] },
      range: { type: Boolean, default: false },
      disabledDates: { type: Array, default: () => [] },
      enabledDates: { type: Array, default: () => [] },
      dark: { type: Boolean, default: false },
      month: { type: Object, default: null },
      height: { type: Number, default: null },
      noShortcuts: { type: Boolean, default: null },
      firstDayOfWeek: { type: Number, default: null },
      customShortcuts: { type: Array, default: () => [] },
      visible: { type: Boolean, default: null },
      monthsOnly: {
        type: Boolean,
        default: false
      }
    },
    data () {
      return {
        transitionDaysName: 'slidenext',
        transitionLabelName: 'slidevnext',
        selectingYearMonth: null,
        isKeyboardActive: true
      }
    },
    computed: {
      bgStyle () {
        return {
          backgroundColor: this.color
        }
      },
      endEmptyDays () {
        const getDays = this.monthDays.length + this.weekStart > 35
        const number = getDays ? 42 : 35
        return number - this.monthDays.length - this.weekStart
      },
      monthDays () {
        return this.month.getMonthDays()
      },
      weekStart () {
        return this.month.getWeekStart()
      },
      monthFormatted () {
        return `${this.month.getFormatted()}`
      },
      year () {
        return `${this.month.getYear()}`
      },
      weekDays () {
        return getWeekDays(this.locale, this.firstDayOfWeek)
      }
    },
    methods: {
      isKeyboardSelected (day) {
        return day && this.newValue
          ? day.format('YYYY-MM-DD') === this.newValue.format('YYYY-MM-DD')
          : null
      },
      isToday (day) {
        return moment(day.format('YYYY-MM-DD')).isSame(
          moment().format('YYYY-MM-DD')
        )
      },
      isDisabled (day) {
        return (
          this.isDateDisabled(day) ||
          !this.isDateEnabled(day) ||
          this.isBeforeMinDate(day) ||
          this.isAfterEndDate(day) ||
          this.isDayDisabledWeekly(day) ||
          (this.isWeekEndDay(day) && this.noWeekendsDays)
        )
      },
      isDateDisabled (day) {
        return this.disabledDates.indexOf(day.format('YYYY-MM-DD')) > -1
      },
      isDateEnabled (day) {
        return (
          this.enabledDates.length === 0 ||
          this.enabledDates.indexOf(day.format('YYYY-MM-DD')) > -1
        )
      },
      isBeforeMinDate (day) {
        return day.isBefore(moment(this.minDate, 'YYYY-MM-DD'))
      },
      isAfterEndDate (day) {
        return moment(day).isAfter(this.maxDate)
      },
      isSelected (day) {
        const date = [
          ...(this.value && this.value.start
            ? [moment(this.value.start).format('YYYY-MM-DD')]
            : this.range
              ? []
              : [moment(this.value).format('YYYY-MM-DD')]),
          ...(this.value && this.value.end
            ? [moment(this.value.end).format('YYYY-MM-DD')]
            : this.range
              ? []
              : [moment(this.value).format('YYYY-MM-DD')])
        ]
        return date.indexOf(day.format('YYYY-MM-DD')) > -1
      },
      isBetween (day) {
        const range =
          this.value && this.value.end
            ? moment
              .range(moment(this.value.start), moment(this.value.end))
              .contains(day)
            : false
        return range
      },
      firstInRange (day) {
        return this.value && this.value.start
          ? moment(moment(this.value.start).format('YYYY-MM-DD')).isSame(
            day.format('YYYY-MM-DD')
          )
          : false
      },
      lastInRange (day) {
        return this.value && this.value.end
          ? moment(moment(this.value.end).format('YYYY-MM-DD')).isSame(
            day.format('YYYY-MM-DD')
          )
          : false
      },
      isDayDisabledWeekly (day) {
        const dayConst = moment(day).day()
        return this.disabledWeekly.indexOf(dayConst) > -1
      },
      isWeekEndDay (day) {
        const dayConst = moment(day).day()
        const weekendsDaysNumbers = [6, 0]
        return this.noWeekendsDays
          ? weekendsDaysNumbers.indexOf(dayConst) > -1
          : false
      },
      selectDate (day) {
        if (this.range && !this.noShortcuts) {
          this.$refs['range-shortcuts'].selectedShortcut = null
        }
        if (this.range) {
          if (
            !this.value.start ||
            this.value.end ||
            day.isBefore(moment(this.value.start))
          ) {
            this.value.start = day.format('YYYY-MM-DD')
            this.value.end = null
          } else {
            this.value.end = day.format('YYYY-MM-DD')
          }
          this.$emit('input', this.value)
        } else {
          this.$emit('input', moment(day).format('YYYY-MM-DD'))
        }
      },

      changePeriod (val) {
        this.transitionDaysName = `slide${val}`
        this.transitionLabelName = `slidev${val}`

        if (this.monthsOnly) {
          this.selectYearMonth(
            {
              month: this.month.month,
              year: val === 'next' ? this.month.year + 1 : this.month.year - 1
            })
          return
        }
        this.$emit('change-month', val)
      },
      selectYearMonth (event) {
        const { month, year } = event
        const isBefore =
          year === this.month.year
            ? month < this.month.month
            : year < this.month.year
        this.transitionLabelName = isBefore ? `slidevprev` : `slidevnext`
        this.selectingYearMonth = null
        this.$emit('change-year-month', event)

        if (this.monthsOnly) {
          this.selectDate(moment(new Date(year, month, 1)))
          this.selectDate(moment(new Date(year, month + 1, 0)))
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
.datepicker-container {
  width: 260px;
  padding: 0 5px;
  position: relative;
  &.range.has-shortcuts {
    width: 400px;
  }

  &.p-0 {
    padding: 0;
  }
  .padding-button {
    padding: 5px 3px !important;
  }
  .calendar {
    position: relative;
  }
  .datepicker-controls {
    height: 56px;
    .arrow-month {
      flex: 0 0 40px;
    }
    .datepicker-button {
      background: transparent;
      cursor: pointer;
      padding: 0 10px;
      border: none;
      outline: none;
      svg {
        height: 17px;
        width: 17px;
        fill: #2c3e50;
      }
      &.datepicker-prev {
        text-align: left !important;
      }
      &.datepicker-next {
        text-align: right !important;
      }
    }
    .datepicker-container-label {
      text-transform: capitalize;
      font-size: 16px;
      position: relative;
      height: 56px;
      overflow: hidden;
    }
    .date-buttons {
      text-transform: capitalize;
      font-weight: 400;
    }
  }
  .month-container {
    position: relative;
    overflow: hidden;
  }
  .datepicker-days {
    display: flex;
    display: -ms-flexbox;
    overflow: hidden;
    flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    .datepicker-day {
      height: 41px;
      flex-grow: 1;
      width: calc(100% / 7);
      position: relative;
      border: none;
      background: transparent;
      font-size: 13px;
      outline: none;
      &.enable {
        cursor: pointer;
      }

      &-effect,
      .datepicker-today {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        height: 30px;
        width: 30px;

        border-radius: 4px;
        -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
        transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
      }

      .datepicker-day-effect {
        margin: auto;
        opacity: 0.6;
        background: dodgerblue;
        transform: scale(0);
      }
      .datepicker-today {
        background-color: #eaeaea;
      }
      .datepicker-day-text {
        position: relative;
        color: #000;
      }
      .datepicker-day-keyboard-selected {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        height: 26px;
        width: 26px;
        opacity: (0.7);
        border-radius: 50%;
        -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
        transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
        background-color: #afafaf;
      }
      &:hover {
        .datepicker-day-text {
          color: #fff;
        }
        .datepicker-day-effect {
          transform: scale(1);
          opacity: 0.6;
        }
      }

      &.between {
        .datepicker-day-text {
          color: #fff;
        }
        .datepicker-day-effect {
          transform: scale(1);
          opacity: 0.5;
          border-radius: 0;
          width: 100%;
        }
        &.first .datepicker-day-effect {
          border-top-left-radius: 4px;
          border-bottom-left-radius: 4px;
        }
        &.last .datepicker-day-effect {
          border-top-right-radius: 4px;
          border-bottom-right-radius: 4px;
        }
        .datepicker-day-keyboard-selected,
        &.first .datepicker-day-keyboard-selected,
        &.last .datepicker-day-keyboard-selected {
          background-color: rgba(0, 0, 0, 0.66);
        }
      }
      &.selected {
        .datepicker-day-text {
          color: #fff;
          font-weight: bold;
        }
        .datepicker-day-effect {
          transform: scale(1);
          opacity: 1;
        }
        .datepicker-day-keyboard-selected {
          background-color: rgba(0, 0, 0, 0.66);
        }
      }
      &.disabled {
        .datepicker-day-text {
          color: #ccc;
        }
        &.selected {
          color: #fff;
        }
        .datepicker-day-effect {
          transform: scale(0);
          opacity: 0;
        }
      }
    }
  }
  &.is-dark {
    .datepicker-days .datepicker-day:not(.between):not(.selected) {
      .datepicker-day-text {
        color: #fff;
      }
      &.disabled .datepicker-day-text {
        color: lighten(#424242, 20%);
      }
    }
    .datepicker-label {
      color: white;
    }
    .text-muted {
      color: lighten(#424242, 40%) !important;
    }
    .datepicker-button {
      svg {
        fill: #fff;
      }
    }
    .datepicker-today {
      background-color: darken(#424242, 10%) !important;
    }
  }
}
@media screen and (max-width: 415px) {
  .datepicker-container {
    width: 100%;
    &:not(.inline) {
      .datepicker-controls {
        height: 36px !important;
      }
    }
    &.range.has-shortcuts {
      width: 100%;
    }
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    flex-flow: column;
    -moz-flex-direction: column;
  }
}
</style>
